.App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  .bgg{
  
    /* background-size: "cover"; */
    height: 300px;
    width: 300px;
  }
  .bgg:hover{
    transform: scale(1.075);
    transition: 0.3s ease-in;
    cursor: pointer;
    
  }
  .bg{
    height: 256px;
    width: 256px;
  }
  .bg:hover{
    transform: scale(1.075);
    transition: 0.3s ease-in;
    cursor: pointer;
    
  }
  .a{
    background-image: url("./components/img/project.png");
    background-size: cover;
    background-repeat: no-repeat;
  }
  .b{
    background-image: url("./components/img/research.png");
    background-size: cover;
    background-repeat: no-repeat;
  }
  .c{
    background-image: url("./components/img/data.png");
    background-size: cover;
    background-repeat: no-repeat;
  }
  
